import React from 'react'

const BannerFormations = (props) => (
    <section id="banner" className="style2">
        <div className="inner">
            <header className="major">
                <h1>Formations</h1>
            </header>
            <div className="content">
                <p>Offre de formations de notre groupe.</p>
            </div>
        </div>
    </section>
)

export default BannerFormations