import React from 'react'
import { Link } from 'gatsby'
import Helmet from 'react-helmet'
import Layout from '../components/layout'
import BannerFormations from '../components/BannerFormations'

import pic08 from '../assets/images/pic08.jpg'
import pic09 from '../assets/images/pic09.jpg'
import pic10 from '../assets/images/pic10.jpg'

const Formations = (props) => (
    <Layout>
        <Helmet>
            <title>IA Responsable PSL - Formations</title>
            <meta name="description" content="Formations" />
        </Helmet>

        <div id="main" className="alt">

            <section id="one">
            <div className="inner">
                <header className="major">
                    <h1>Formations</h1>
                </header>
                <p>Cours organisés au sein de PSL par les membres de notre groupe.</p>
                <div className="grid-wrapper">
                    <div className="col-4">
                        <div class="box">
                        <h3>Humanités digitales</h3>
                            <p>M1 SIREN</p>
                            <p>Intervenants : </p>
                            <ul className="actions">
                                        <li><a href="#" className="button special">En savoir plus</a></li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-4">
                        <div class="box">
                            <h3>Ethique de l'IA</h3>
                            <p>M2 IASD</p>
                            <p>Intervenants : </p>
                            <ul className="actions">
                                        <li><a href="#" className="button special">En savoir plus</a></li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-4">
                        <div class="box">
                            <h3>Formation C</h3>
                            <p>M1 SIREN</p>
                            <p>Intervenants : </p>
                            <ul className="actions">
                                        <li><a href="#" className="button special">En savoir plus</a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            </section>
        </div>
    </Layout>
)

export default Formations